.App {
  text-align: center;
  font-family: monospace;
}

body {
  margin: 0;
  padding: 0;
 /* box-sizing: border-box; */
}

.title {
  width: 100%;
  font-size: 22px;
  margin: 0;
}

.newsContainer {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.newsContainer .article {
  width: 700px;
  border: 2px solid black;
  margin: 25px;
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
}

.newsContainer .article img {
width: 500px;
}

.newsContainer .article p {
  width: 600px;
}

